body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.dark-mode {
  background-color: #121212;
  color: #ffffff;
}
body.light-mode {
  background-color: #ffffff;
  color: #000000;
}
/* Custom Scrollbar Styles */
body::-webkit-scrollbar {
  width: 20px; /* Width of the vertical scrollbar */
  height: 12px; /* Height of the horizontal scrollbar */
}
body::-webkit-scrollbar-track {
  background: #333; /* Track color */
}
body::-webkit-scrollbar-thumb {
  background-color: goldenrod; /* Scrollbar thumb color */
  border-radius: 10px; /* Roundness of the scrollbar thumb */
  border: 3px solid #333; /* Padding around the scrollbar thumb */
}
body::-webkit-scrollbar-thumb:hover {
  background-color: #ffcc00; /* Scrollbar thumb color on hover */
}
p {
  color: whitesmoke;
  font-size: 20px;
  padding: 0px 20px 0px 20px;
  margin: 10px auto;

}

body.dark-mode p {
  color: #ffffff;
  text-shadow: 1px 1px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;

}

body.light-mode p {
  color: #333333;
  text-shadow: 1px 1px 0 #ffffff, -1px -1px 0 #ffffff, 1px -1px 0 #ffffff, -1px 1px 0 #ffffff, 1px 1px 0 #ffffff;
}
span {
  color: goldenrod;
}
h1 {
  color: whitesmoke;
  text-shadow: 2px 2px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  font-size: 60px;
  padding: 20px 20px 20px 20px;
}

h2 {
  color: whitesmoke;
  font-size: 20px;
  padding: 10px 10px 10px 10px;
}
h3{
  font-size: 40px;
  padding: 20px 20px 20px 20px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar {
  background: linear-gradient(to right, #000000, #6610f2);
  height: 75px;
  font-size: 25px;
  color: white;
  text-shadow: 0 2px 0 #000000;
}
.navbar:hover {
  background: linear-gradient(to right, goldenrod, #6610f2);
  /*cursor: pointer;*/
  color: white;
  text-shadow: 0 2px 0 #000000;
  /*transform: scale(1.0);  Slightly increase the size */
  transition: transform 0.3s ease; /* Smooth transition */
  box-shadow: 0px 0px 25px 5px goldenrod; /* Glow effect */
}
/* Glow effect at the bottom of the navbar */
.navbar::after {
  content: ''; /* Empty content */
  padding: 0px 0px 0px 0px; 
  position: absolute; 
  bottom: 0; /* Position at the bottom of the navbar */
  left: 0; 
  width: 100%; /* Full width */
  height: 0px; /* Height of the glow effect */
}
/* General Tooltip Styles */
.nav-item {
  position: relative; /* Ensure tooltips are positioned relative to the nav items */
  cursor: pointer;
}
.nav-item:hover .tooltip {
  opacity: 1;
  transform: translateX(-50%) translateY(-5px); /* Add slight animation */
}
.icon-border {
  display: inline-block;
  padding: 0px 0px 0px 0px;
  margin:  5px 0px 5px 0px;
  height: 40px;
  width:  40px;
  background-color: #6610f2;
  border: 1px solid white;
  border-radius: 8px;
  transition: all 0.3s ease;
  text-align: center;
}
.icon-border:hover {
/*background-color: white;*/  
  border: 4px solid goldenrod;
  transform: scale(1.3);
}
.icon-border i {
  font-size: 20px;
  padding: 10px;
  color: white;
}
.icon-border:hover i {
  color: goldenrod;
}
.tooltip {
  position: absolute;
  bottom: -50%; /* Position above the nav link */
  left: 50%;
  transform: translateX(-50%);
  background: #333;
  color: white;
  padding: 10px 10px;
  border-radius: 20px;
  font-size: 12px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;
}
/* Tooltip Arrow */
.tooltip::after {
  content: '';
  position: absolute;
  top: -100%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 5px;
  border-style: solid;
  border-color: #33333310 transparent transparent transparent;
}
@media (max-width: 600px) {
  .tooltip {
    bottom: 0%; /* Position closer to the nav link */
    left: 25%;
    font-size: 15px; /* Smaller font size for better fit */
    padding: 10px 10px 10px 10px; /* Adjust padding for smaller screens */
  }
  .nav-item:hover .tooltip {
    transform: translateX(-40%) translateY(5px); /* Adjust animation */
  }

  .nav-item {
    margin: 10% 0% 0% 5%; /* Increase the margin between nav items */
    height: 50px; /* Increase the height of the navbar */
  }

}
/*  Time Format Toggle Styling  */ 
#timeFormat {
  display: block;
  margin: 20px auto;
  padding: 10px;
  font-size: 0.9rem;
  border: 1px solid #ccc;
  border-radius: 3px;
  background-color: #f8f9fa;
  color: #333;
