.custom-btn {
  margin: 0px 0px 10px 0px;
  padding: 20px 20px 20px 20px;
  height: 60px;
  width: 250px;
  font-size: 1rem;
  color: #fff;
  background: linear-gradient(135deg, #000000, #6610f2); /* Gradient background */
  text-align: center;
  text-decoration: none; /* Removes underline from the link */  
  text-shadow: 0 2px 0 #000000;
  border: 2px solid black;
  border-radius: 30px;
  display: inline-block;
  transition: all 0.3s ease; /* Smooth transition for hover effect */
  cursor: pointer;
}
.custom-btn:hover {
  background: linear-gradient(135deg, #ffa633, #6610f2); /* Lighter gradient on hover */
  transform: translateY(-3px); /* Slight lifting effect on hover */
  text-shadow: 0 2px 0 #000000;
  color:whitesmoke;
  border: solid;
  border-color: #BA9653;
}
.custom-btn:active {
  transform: translateY(1px); /* Button pushes down slightly on click */
}
.custom-btn:focus {
  outline: none; /* Removes the focus outline */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Adds a shadow when focused */
}
/* COOKIE CONTENT */
#cookieConsent {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #222;
  color: #fff;
  padding: 20px;
  text-align: center;
  z-index: 1000;
  display: none;
  opacity: 0;
  transition: opacity 0.5s ease;
  border-radius: 10px;
  width: 70%;
  max-width: 500px;
  height: 300px;
  max-height: 500px;
}
#cookieConsent.show {
  display: block;
  opacity: 1;
}
#cookieConsent .cookie-icon {
  font-size: 40px;
  color: whitesmoke;
  margin: 20px 20px 20px 20px; 
  vertical-align: middle;
}
#cookieConsent .cookie-icon:hover {
  font-size: 60px;
  color:goldenrod;
  margin: 20px 20px 20px 20px; 
  vertical-align: middle;
}
#cookieConsent p {
  display: inline-block;
  margin: 0;
  font-size: 16px;
}
#cookieConsent button {
  background-color: whitesmoke;
  color: black;
  text-shadow: 2px #222;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 25px;
