  margin: 0px 20px 20px 20px;
  height: 100px;
  width: 150px;
  cursor: pointer;
 
  /* Padding in rem units 
  padding: 1rem;*/

  /* Padding in px units 
  padding: 16px;*/

  /* Padding in percentage units 
   padding: 10%;*/

  /* Padding with different values for each side (top, right, bottom, left) */
 /*  padding: 1rem 2rem 1rem 2rem;  top, right, bottom, left */

  /* Padding with different values for vertical and horizontal sides */
  /* padding: 1rem 2rem;  vertical, horizontal */

  transition: transform 0.2s;
}

.skill-box:hover {
  transform: scale(1.1); /* Slight zoom effect on hover */
