/*  CHANGELOG STYLING STARTS */
#changelog-component {
  padding: 200px 0px 200px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
/*border: 4px solid whitesmoke; */
} 
.changelog {
  transition: transform 0.2s ease-in-out, box-shadow 0.3s ease-in-out, background-color 0.3s ease-in-out;
  max-width: 1000px;
  max-height: 1000px;  
  width:  600px;
  height: 650px;
  background-color: #333;
  margin:  20px auto;
  padding: 20px 20px 20px 20px ;
  border: 4px solid black;
  border-radius: 0px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  text-align: center;
  overflow: hidden; /* Prevent content overflow */
  position: relative;
}
.changelog:hover {
  transform: translateY(-10px); /* Slight lift effect on hover */
  color: white;
  background-color: black;
  border: 4px solid goldenrod;
  box-shadow: 0 0 16px 8px goldenrod;  
}
.changelog-title {
  font-size: 40px;
  font-weight: bold;
  margin: 0px auto;
  color: white;
  text-shadow: 2px 2px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.changelog-subtitle {
  font-size: 20px;
  padding: 20px 20px 20px 20px;
  margin: 0px;
}
.changelog-timeline {
  list-style: none; 
  padding: 20px auto;
  margin:  20px auto;
  position: relative;
  height: calc(100% - 150px); /* Keep it within bounds */
  overflow-y: auto; /* Add scrolling for overflow content */
}
.changelog-timeline::before {
  content: "";
  position: absolute;
  top:    2%;  
  left:   43.5%;
  right:  49.5%;  
  bottom: 8%;
  border-radius: 20px;
  width:  10px;
  background-color: mediumslateblue;
}
.timeline-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0px 0;
}
.timeline-item::before {
  content: "";
  position: absolute;
  left: 44.5%;
  width: 20px;
  height: 20px;
  background-color: goldenrod;
  border: 4px solid mediumslateblue;
  border-radius: 50%;
  transform: translate(-50%, 0);
}
.timeline-date {
  color: goldenrod;
  width: 40%;
  text-align: justify;
  font-weight: bold;
  padding: 20px 20px 20px 0px;
}
.timeline-description {
  color: mediumslateblue;
  width: 55%;
  text-align: justify;
  padding: 10px 10px 10px 10px; 
}
.changelog-button:hover {
  background: linear-gradient(135deg,#ffa633, #6610f2); /* Lighter gradient on hover */
  text-shadow: 0 2px 0 black;
}
/* Responsive Design */
@media (max-width: 768px) {

  .changelog {
    Width: 90%;
    height: 700px;
  }
  .changelog-timeline::before {
    content: "";
    position: absolute;
    top:    2%;  
    left:   43.5%;
    right:  49.5%;  
    bottom: -15%;
    padding: 0px 0px 20px 0px;
    border-radius: 20px;
    width:  10px;
    background-color: mediumslateblue;
  }
  .timeline-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0px 0;
  }
  .timeline-item::before {
    content: "";
    position: absolute;
    left: 45%;
    width: 20px;
    height: 20px;
    background-color: goldenrod;
    border: 4px solid mediumslateblue;
    border-radius: 50%;
    transform: translate(-50%, 0);
  }
  .timeline-date {
    color: goldenrod;
    white-space: 'text-wrap';
    width: 30%;
  }
  .timeline-description {
    color: mediumslateblue;
    font-size: smaller;
    width: 60%;
    text-align: center;
    padding: 10px 10px 10px 10px; 
  }
}
/*  CHANGELOG STYLING ENDS */
footer {
  background: linear-gradient(to left, #000000, #6610f2);
  color: white;
}
footer:hover {
  background: linear-gradient(to left, goldenrod, #6610f2);
  color: white;
  text-shadow: 0 2px 0 #000000;
  transform: scale(1.0); /* Slightly increase the size */
  transition: transform 0.3s ease; /* Smooth transition */
  box-shadow: 0px 0px 25px 5px goldenrod; /* Glow effect */

}
footer i {
  color: #adb5bd;
}
footer i:hover {
  color: #ffffff;
  transform: scale(2.0); /* Slight zoom effect on hover */
  transition: transform 0.2s ease; /* Smooth transition */
}